  Status["CREATING_TAG"] = "creating-tag";
  Status["DONE"] = "done";
})(Status || (Status = {}));
const restrictSystemTagsCreationToAdmin = loadState('systemtags', 'restrictSystemTagsCreationToAdmin', false);
export default defineComponent({
  name: 'SystemTagPicker',
  components: {
    CheckIcon,
    CircleIcon,
    CircleOutlineIcon,
