    NcCheckboxRadioSwitch,
    // eslint-disable-next-line vue/no-unused-components
    NcChip,
    NcColorPicker,
    NcDialog,
    NcEmptyContent,
    NcLoadingIcon,
    NcNoteCard,
    NcTextField,
    PencilIcon,
    PlusIcon,
    TagIcon
