      const lastTagRemove = tagsRemove.pop();
      const addStringSingular = n('systemtags', '{tag} will be set to 1 file.', '{tag} will be set to {count} files.', this.nodes.length, {
        tag: lastTagAdd,
        count: this.nodes.length
      }, {
        escape: false
