      const removeStringPlural = n('systemtags', '{tags} and {lastTag} will be removed from 1 file.', '{tags} and {lastTag} will be removed from {count} files.', this.nodes.length, {
        tags: tagsRemove.join(', '),
        lastTag: lastTagRemove,
        count: this.nodes.length
      }, {
        escape: false
      });
      // Singular
      if (this.toAdd.length === 1 && this.toRemove.length === 0) {
